    import {couponList, couponReceive} from "@/api/userInfo";
    import rfLoadMore from '@/components/rf-load-more/rf-load-more';
    import moment from '@/common/moment';
    export default {
        components: {
            rfLoadMore
        },
        data() {
            return {
                couponList: [],
                type: null,
                loadingType: 'more',
                page: 1,
                loading: true,
                errorInfo: ''
            }
        },
        filters: {
            time(val) {
              return moment(new Date(val).getTime()).format('YYYY-MM-DD HH:mm')
            }
        },
        onLoad(options) {
            this.type = options.type;
            this.initData();
        },
        //下拉刷新
        onPullDownRefresh() {
            this.page = 1;
            this.couponList.length = 0;
            this.getCouponList('refresh');
        },
        //加载更多
        onReachBottom() {
            this.page++;
            this.getCouponList();
        },
        methods: {
            // 数据初始化
            initData() {
                this.getCouponList();
            },
            //获取收货地址列表
            async getCouponList(type) {
                await this.$http.post(`${couponList}`, {
					keyName : type,
                    startPage: this.page,
					pageSize : 10
                }).then(r => {
                    this.loading = false;
                    if (type === 'refresh') {
                        uni.stopPullDownRefresh();
                    }
                    this.loadingType = r.data.length === 10 ? 'more' : 'nomore';
                    this.couponList = [...this.couponList, ...r.data];
                }).catch(err => {
                    this.couponList.length = 0;
                    this.errorInfo = err;
                    this.loading = false;
                    if (type === 'refresh') {
                        uni.stopPullDownRefresh();
                    }
                })
            },
            // 获取优惠券
            async getCoupon(item) {
                if (this.type) return;
                // 优惠券是否可领取 is_get 0 不可领取
                if (parseInt(item.count, 10) === 0) {
                    this.$mHelper.toast('该优惠券不可领取');
                    return;
                }
                await this.$http.post(`${couponReceive}`, {
                    keyName: item.id
                }).then(r => {
					console.log('12312')
					if(r.data == 1){
						this.$mHelper.toast('领取成功');
						this.loading = true;
						this.page = 1;
						this.couponList.length = 0;
						this.getCouponList();
					}else if(r.data == 2){
						this.$mHelper.toast('您已领取了当前优惠券');
					}else if(r.data == 3){
						this.$mHelper.toast('对不起,优惠券已被他人领取');
					}
                })
            }
        }
    }
